import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { ShareValueService } from '../share-value.service';

@Component({
  selector: 'app-call-screen',
  templateUrl: './call-screen.component.html',
  styleUrls: ['./call-screen.component.css']
})
export class CallScreenComponent implements OnInit {

  @Input() classTwo;
  @Input() phoneNumber;
  @Input() phoneName;
  @Input() phoneImg;
  @Output() sendToEvent = new EventEmitter<any>();
  service : ShareValueService;
  private interval;
  private callCount : number;

  constructor(serve : ShareValueService) { 
    this.classTwo = 'onhide';
    this.service = serve;
    this.callCount = 0;
  }

  ngOnInit() {

  }

  sendToEventMyParent(){
    console.log(this.phoneNumber);
    this.classTwo = 'onhide';
    this.sendToEvent.emit("IM_HIDE");
    this.service.headerShow = true;
    this.phoneNumber = null;
    this.service.imWorking = false;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes.phoneNumber != undefined && changes.phoneNumber != null && changes.phoneNumber.currentValue != null && changes.phoneNumber.currentValue.length >= 1){      
      this.service.imWorking = true;
      this.interval = setInterval(()=>{
        if(this.callCount >= 60){
          clearInterval(this.interval);
          this.callCount = 0;
          this.sendToEventMyParent();
        } else{
          this.callCount++;
        }
      },1000);
    } else{
      if(this.interval != undefined && this.interval != null){
        clearInterval(this.interval);
        this.callCount = 0;
      }
    }
  }
}
