import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ShareValueService {
  
  public headerShow : boolean = true;
  public audio;

  public headerOne = 'nonselected';
  public headerTwo = 'selected';
  public headerThree = 'nonselected';
 
  public oneBool = false;
  public twoBool = true;
  public threeBool = false;

  public imWorking = false;
  public clickSounds =new Map();


  constructor() { 
    this.audio = new Audio();
    //this.audio.src='assets/audio.mp3';
    this.audio.src='assets/ohlle.mp3';
    for(var i=0;i < 12;i++){  //따로 객체를 사용하기위해
        const aud = new Audio();
	aud.src='assets/click.mp3';
	aud.load();
	if(i == 10){
	    this.clickSounds.set('#',aud);
	} else if(i == 11){
	    this.clickSounds.set('*',aud);
	} else {
	    this.clickSounds.set(''+i+'',aud);
	}
    }
  }
}
