import { ElementRef,Directive, HostListener,Input } from '@angular/core';
import { ShareValueService } from './share-value.service';

@Directive({
    selector: '[appHighlight]'
})

export class TouchDirective {
    private service : ShareValueService;

    constructor(private el : ElementRef, serve : ShareValueService) { 
	this.service = serve;
	console.log(this.service);
    }
    @Input('appHighlight') arg : string;
    private touch1 = {x:0,y:0,time:0};

    doSwipe(evt,type){
        //console.log(evt,type,'gogo!');
	this.forDirectiveOrder(type);
    }
  
  forDirectiveOrder(type){
    console.log(this.service.imWorking);
    if(this.service.imWorking){
	return;
    } else{
    if(type == 'left'){
	if(this.service.oneBool){
	    this.service.headerOne = "nonselected"; this.service.headerTwo = "nonselected"; this.service.headerThree = "selected";
	    this.service.oneBool = false; this.service.twoBool = false; this.service.threeBool = true;
	} else if(this.service.threeBool){
	    this.service.headerOne = "nonselected"; this.service.headerTwo = "selected"; this.service.headerThree = "nonselected";
	    this.service.oneBool = false; this.service.twoBool = true; this.service.threeBool = false;
	} else{
	    this.service.headerOne = "selected"; this.service.headerTwo = "nonselected"; this.service.headerThree = "nonselected";		
	    this.service.oneBool = true; this.service.twoBool = false; this.service.threeBool = false;
	}
    } else{
	if(this.service.twoBool){
	    this.service.headerOne = "nonselected"; this.service.headerTwo = "nonselected"; this.service.headerThree = "selected";
	    this.service.oneBool = false; this.service.twoBool = false; this.service.threeBool = true;
	} else if(this.service.oneBool){
	    this.service.headerOne = "nonselected"; this.service.headerTwo = "selected"; this.service.headerThree = "nonselected";
	    this.service.oneBool = false; this.service.twoBool = true; this.service.threeBool = false;
	} else{
	    this.service.headerOne = "selected"; this.service.headerTwo = "nonselected"; this.service.headerThree = "nonselected";		
	    this.service.oneBool = true; this.service.twoBool = false; this.service.threeBool = false;
	}
    }    
    }

  }


    @HostListener('touchstart', ['$event'])
    @HostListener('touchend', ['$event'])
    @HostListener('touchcancel', ['$event'])
    handleTouch(ev){
	var touch = ev.touches[0] || ev.changedTouches[0];
        if (ev.type === 'touchstart'){
          this.touch1.x = touch.pageX;
          this.touch1.y = touch.pageY;
          this.touch1.time = ev.timeStamp;
        } else if (ev.type === 'touchend'){
          var dx = touch.pageX - this.touch1.x;
          var dy = touch.pageY - this.touch1.y;
          var dt = ev.timeStamp - this.touch1.time;
        if (dt < 500){
            // swipe lasted less than 500 ms
            if (Math.abs(dx) > 120){
              // delta x is at least 60 pixels
              if (dx > 0){
                this.doSwipe(ev,'left');
              } else {
                this.doSwipe(ev,'right');
              }
            }
          }
	} 
    }
    
}