
import { trigger, animate, transition, style, query } from '@angular/animations';

export const fadeAnimation = trigger('fadeAnimation', [
  transition('* <=> *', [
    query(':enter, :leave', style({ position: 'fixed', width:'100%' })
    , { optional: true }),    
    query(
      ':enter',
      [style({ transform: 'translateX(100%)',opacity:0  }),
      animate('0.15s ease-in-out', style({ transform: 'translateX(0%)',opacity:0 }))],
      { optional: true }
    ),
    query(
      ':leave',
      [style({ transform: 'translateX(0%)' }), animate('0.15s ease-in-out', style({ transform: 'translateX(-100%)' }))],
      { optional: true }
    )
  ])
]);


