import { Component, OnInit, Directive } from '@angular/core';
import {ShareValueService} from '../share-value.service';
import { Howl } from 'howler'
@Component({
  selector: 'app-middle-menu',
  templateUrl: './middle-menu.component.html',
  styleUrls: ['./middle-menu.component.css'],
})


export class MiddleMenuComponent implements OnInit {
  
  private service : ShareValueService;

  private phone_number : string;
  private keypadNum = [1,2,3,4,5,6,7,8,9,'*',0,'#'];
  private phoneImg = "assets/img/phone.png";
  private cancleImg = "assets/img/cancle.png";

  private targetNumber ;
  private targetName ='누굴까?';
  private targetImg = 'assets/familly/smile.png';
  private classOne = 'onshow';
  private classTwo = 'onhide';

  constructor(serve : ShareValueService ) { 
    window['referenceFunction'] = {
      component: this
    };      
    this.phone_number = "";
    this.service = serve;
  }
  private sounder = new Howl({
      src: ['assets/click.mp3'],
      html5 :true
  });
  ngOnInit() {

  }

  keypadPress(event){
    let addBar = "";
    this.sounder.play();
    //this.service.clickSounds.get(event.srcElement.innerHTML+"").play();
    if(this.phone_number.length == 3){
      addBar = "-";
    } else if(this.phone_number.length == 8){
      addBar = "-";
    }
    this.phone_number += addBar +event.srcElement.innerHTML;    
  }

  remove(){
    this.phone_number = this.phone_number.substr(0,this.phone_number.length -1);
  }

  change(){
    if(this.phone_number != undefined && this.phone_number != null && this.phone_number.length > 0){
      this.targetNumber = this.phone_number;
      this.classOne = 'onhide';
      this.classTwo = 'onshow';
      this.service.headerShow = false;

      this.service.audio.load();
      this.service.audio.play();
    }
  }

  receiveEvent(event){
    this.service.headerShow = true;
    this.classOne = "onshow";
    this.classTwo = "onhide";
    this.service.audio.pause();
  }
}
